import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { isIE } from 'react-device-detect'
import {
  Menu,
  Image,
  Card,
  Breadcrumb,
  Segment,
  Label,
  Icon,
} from 'semantic-ui-react'
import { getWeather } from '../reducers/weatherReducer'
import { getTimeZone } from '../reducers/timeZoneReducer'
import {
  initializeCountries,
  filterCountriesBySubRegion,
  filterCountriesByRegion,
} from '../reducers/countriesReducer'
import zoomLevel from '../zoomLevels'
import loadable from '@loadable/component'
import WeatherIE from '../components/WeatherIE'

const News = loadable(() => import('../components/News'))
const Map = loadable(() => import('../pages/Map'))
const Weather = loadable(() => import('../components/Weather'))

const Country1 = ({ geo }) => {
  const [loading, setLoading] = useState(true)
  const [activeTab, setActiveTab] = useState('Map')

  const dispatch = useDispatch()

  const state = useSelector((state) => state.countries)
  const country = useSelector((state) => state.countries.filtered[0])
  const articles = useSelector((state) => state.news.news.articles)

  const unit = useSelector((state) => state.unit.unit)
  const location = country.latlng

  const mapFeatures = useSelector((state) => {
    if (state.country.features) {
      return state.country.features.features
    }
  })

  useEffect(() => {
    setLoading(false) //remove after map update
    dispatch(
      getWeather({
        location: location,
        unit: unit,
      })
    )
    dispatch(getTimeZone({ location: location }))
  }, [unit])

  const handleTabClick = (e, { name }) => {
    setActiveTab(name)
  }

  const handleBreadcrumb = (val) => {
    if (val === 'All') {
      dispatch(initializeCountries())
    } else if (val === 'region') {
      dispatch(filterCountriesByRegion(state.filter.region))
    } else if (val === 'subregion') {
      dispatch(filterCountriesBySubRegion(state.filter.subregion))
    }
    setLoading(true)
  }

  return loading ? (
    <>
      <Icon loading name="circle notch" />
    </>
  ) : (
    <>
      <Segment attached="bottom">
        <Menu id="breadcrumb" secondary>
          <Breadcrumb size="small">
            <Breadcrumb.Section
              key="All"
              link
              onClick={() => handleBreadcrumb('All')}
            >
              All
            </Breadcrumb.Section>
            <Breadcrumb.Divider icon="right chevron" />
            <Breadcrumb.Section
              key={country.region}
              link
              onClick={() => handleBreadcrumb('region')}
            >
              {country.region}
            </Breadcrumb.Section>
            {country.subregion !== '' ? (
              <>
                <Breadcrumb.Divider icon="right chevron" />
                <Breadcrumb.Section
                  key={country.subregion}
                  link
                  onClick={() => handleBreadcrumb('subregion')}
                >
                  {country.subregion}
                </Breadcrumb.Section>
                <Breadcrumb.Divider icon="right chevron" />
                <Breadcrumb.Section key={country.ccn3} active>
                  {country.name.common}
                </Breadcrumb.Section>
              </>
            ) : (
              <></>
            )}
          </Breadcrumb>
        </Menu>
        <Menu attached tabular className="ui menu2">
          <Menu.Item
            name="Map"
            active={activeTab === 'Map'}
            onClick={handleTabClick}
          />
          {articles.length > 0 ? (
            <Menu.Item
              name="News"
              active={activeTab === 'News'}
              onClick={handleTabClick}
            />
          ) : (
            <></>
          )}
          <Menu.Item
            name="Flag"
            active={activeTab === 'Flag'}
            onClick={handleTabClick}
          />{' '}
          <Menu.Item
            name="CoatOfArms"
            active={activeTab === 'CoatOfArms'}
            onClick={handleTabClick}
          />
          <Menu.Item
            name="Weather"
            active={activeTab === 'Weather'}
            onClick={handleTabClick}
          />
        </Menu>

        {activeTab === 'Map' ? (
          <>
            {mapFeatures && (
              <Map
                lt={location[0]}
                lg={location[1]}
                zm={zoomLevel(country)}
                features={mapFeatures}
                geo={geo}
              />
            )}
          </>
        ) : (
          <></>
        )}
        {articles && articles.length > 0 && activeTab === 'News' ? (
          <>
            <News />
          </>
        ) : (
          <></>
        )}
        {activeTab === 'Flag' ? (
          <Image
            id="flag"
            centered
            src={isIE ? country.flags.png : country.flags.svg}
            alt="country flag"
            size="medium"
            bordered
          />
        ) : (
          <></>
        )}
        {activeTab === 'CoatOfArms' ? (
          <>
            {Object.entries(country.coatOfArms).length === 0 ? (
              <Label>No image available</Label>
            ) : (
              <Image
                id="coatOfArms"
                centered
                src={isIE ? country.coatOfArms.png : country.coatOfArms.svg}
                alt="coat of arms"
                size="medium"
                bordered
              />
            )}
          </>
        ) : (
          <></>
        )}
        {activeTab === 'Weather' ? (
          <>
            <Card id="CiaCard" fluid>
              {isIE ? (
                <WeatherIE country={country} />
              ) : (
                <Weather country={country} />
              )}
            </Card>
          </>
        ) : (
          <></>
        )}
      </Segment>
    </>
  )
}

const Country = React.memo(Country1)

export default Country
